.myModal{
    position: fixed;
    top: 0;
    bottom: 0;
    right: 0;
    left: 0;
    display: none;
    background: rgba(0, 0, 0, 0.5);
}
.myModalContent{
    min-width: 800px;
    padding: 25px;
    background: #fff;
    border-radius: 16px;
}
.myModal.active{
    display: flex;
    justify-content: center;
    align-items: center;
}