.myLoader{
    width: 100px;
    height: 100px;
    margin: 30px auto;
    border: 4px dashed teal;
    border-radius: 50%;
    animation: rotate 1.5s infinite linear;
}

@keyframes rotate {
    from{
        transform: rotate(0deg) scale(0.7);
    }
    to{
        transform: rotate(360deg) scale(2);
    }
}